<template lang="html">
  <div class="search-go ">
    <!-- 头部 -->
    <div class="search-go-header clearfix">
      <span class="back fl" @click="$router.go(-1)"></span>
      <div class="search-go-input">
        <i class="search__icon"></i>
        <i class="voice-icon"></i>
        <input @input.stop="toSearchKey" v-model="search" type="text" placeholder="请输入关键词">
      </div>
    </div>
    <!-- 搜索结果 -->
    <div class="search-go-bottom">
      <div class="search-hot" v-if="!search">
        <p class="search-go-hot">热门搜索</p>
        <div class="search-hot-list">
          <span v-for="item in hotArr" @click="" class="search-hot-list-item fl">{{item}}</span>
        </div>
      </div>

      <div class="" v-else>
        <white-title v-for="(item, index) in resultArr" @click.native="getSearchItem(item)" :key="index" :text="item.tagName" textColor="#666" :class="{'borderB': index !== resultArr.length-1}"></white-title>
      </div>

      <no-data v-if="this.resultArr.length <= 0 && search"></no-data>
    </div>

  </div>
</template>

<script>
import api from '@/api'
import { mapGetters } from 'vuex'
import minutils from '@/utils/minUtils'
import _ from 'underscore'
import noData from '@/components/common/noData'
import whiteTitle from '@/components/common/whiteTitle'

export default {
  data () {
    return {
      hotArr: [
        '一厅',
        '一房一厅',
        '一房一厅一厅',
        '一房一厅',
        '一房一厅',
        '一房一厅',
        '一房一厅',
        '一房一厅',
        '一房一厅'
      ],
      search: '',
      resultArr: [],
      userId: sessionStorage.getItem('userId')
    }
  },
  computed: {
    ...mapGetters({location: 'getLocation'})
  },
  components: { whiteTitle, noData },
  methods: {
    toSearchKey: _.throttle(function (e) {
      var self = this
      e.target.value && this.ajaxGetResult({locationLon: self.location.lng, locationLat: self.location.lat, keyWord: e.target.value, e: e})
    }, 500),
    // 所有异步请求独立，方便各种方法调用
    ajaxGetResult ({locationLon = 0, locationLat = 0, keyWord = '', e = null} = {}) {
      minutils.IndicatorOpen('加载中...')
      let data = {'locationLon': locationLon, 'locationLat': locationLat, 'keyWord': keyWord}
      this.getSearchResult(data)
    },
    getSearchItem (item) {
      sessionStorage.setItem('searchId', item.userId)
      this.$router.push('/detail')
    },
    getSearchResult (data) {
      api.searchAllTagsByKeyWord(data).then(response => {
        minutils.IndicatorClose()
        let result = response.body
        if (result.isSuccess) {
          this.resultArr = result.data
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
@import '../assets/css/variable.less';

.search-go-header {
  width: 100%;
  height: .88rem;
  overflow: hidden;
  background-color: @inputhdColor;
  border-bottom: 1px solid @lineColor;
  .back {
    display: inline-block;
    width:.44rem;
    height: .44rem;
    margin: .22rem 0 0 .08rem;
    background: url(../assets/img/back@2x.png) no-repeat;
    background-size: .44rem .44rem;
  }
  .search-go-input {
    height: .78rem;
    margin-left: .82rem;
    padding: .1rem .22rem 0 0;
    position: relative;
    input {
      width: 100%;
      height: 100%;
      font-size: .3rem;
      padding-left: .68rem;
      vertical-align: top;
      border: none;
      color: @lightColor;
      background: @inputbgColor;
      &::placeholder {
        color: @lightColor;
      }
    }
    .search__icon {
      top: .2rem;
      left: .12rem;
    }
    .voice-icon {
      position: absolute;
      width: .44rem;
      height: .44rem;
      top: .2rem;
      right: .3rem;
      background: url(../assets/img/voice@2x.png) no-repeat;
      background-size: .44rem .44rem;
      // background-color: #eee;
    }

  }
}
.search-go-bottom {
  // padding: .25rem 0 0 .22rem;
  .search-hot {
    padding: .25rem 0 0 .22rem;
  }
  .search-go-hot, .search-go-result {
    font-size: .32rem;
    line-height: .62rem;
    color: @midleColor;
  }
  .search-go-hot {
    color: @activeColor;
  }
  .search-hot-list {
    background: @whiteColor;
    // padding: 0 .22rem;
    .search-hot-list-item {
      font-size: .32rem;
      height: .88rem;
      line-height: .88rem;
      color: @whiteColor;
      padding: 0 .3rem;
      margin-left: .3rem;
      margin-top: .3rem;
      max-width: 2.6rem;
      min-width: 1.9rem;
      background: @activeColor;
      border-radius: .05rem;
      &:nth-child(3n+1) {
        margin-left: 0;
      }
      &:nth-child(-n+3) {
        margin-top: 0;
      }
    }
  }
}
</style>



